/*App*/
body {
  margin: 0;
  font-family: Arial, sans-serif;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

/*Navbar*/
.navbar {
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: #333;
  color: #fff;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
}

/*HeroSection*/
.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
  background: url('/src/images/logo.jpeg') center/cover no-repeat;
}

.hero-content {
  color: white;
  text-align: center;
}

.shop-now {
  background: #ff6347;
  padding: 0.5rem 1rem;
  color: white;
  border: none;
  border-radius: 5px;
  text-decoration: none;
}

/*ProductSection*/
.products {
  padding: 2rem;
}

.product-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.product-card {
  border: 1px solid #ddd;
  padding: 1rem;
  margin: 1rem;
  text-align: center;
}

/*Footer*/
.footer {
  padding: 1rem;
  background: #333;
  color: #fff;
  text-align: center;
}